import React, { useState, useEffect } from "react";
import { LineChart, Line, ResponsiveContainer, XAxis, YAxis } from 'recharts';

const yVal = 2.45; // Переменная контролирующая Y ось значений

const dataDefault = [
  { x: 1, y: 1, val: 1 },
  { x: 9, y: yVal }
];

const Game = (payload) => {

  const grafdata = payload.data;

  const [value, $value] = useState(1);
  const [data, $data] = useState(dataDefault);
  const [values, $values] = useState([]);
  const [nullValues, $nullValues] = useState([{ x: 9, y: yVal }]);
  let [count, $count] = useState(0);

  useEffect(() => {

    let status = grafdata.gameStatus;

    if (status === 'Game') {
      if (count * 100 / 1000 > 9) {
        if (value > yVal) {
          $nullValues([ { x: count * 100 / 1000, y: value } ]);
        } else {
          $nullValues([ { x: count * 100 / 1000, y: yVal } ]);
        }
      } else {
        if (value > yVal) {
          $nullValues([ { x: 9, y: value } ]);
        } else {
          $nullValues([ { x: 9, y: yVal } ]);
        }
      }

      $values([...values, ...[ { val: value, y: value, x: count * 100 / 1000 } ]]);
      $data([...values, ...nullValues]);
      $value(grafdata.value);

    } else if (status === 'Results') {
      $nullValues([{ x: 9, y: yVal }]);
      $values([]);
      $data(dataDefault);
    }

  }, [value]);

  useEffect(() => {

    $value(grafdata.value);
    $count(grafdata.count);

  });

  return (
    <div className="graph_body">
      <div className="graph_main">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart width="100%" height="100%" data={data} margin={{ top: 5, right: 30, left: -25, bottom: -10 }}>
            <YAxis type="Number" dataKey="y" unit='x' tickCount={yVal * 3} />
            <XAxis type="Number" dataKey="x" tickCount={8} />
            <Line type="basis" dataKey="val" stroke="#fff" strokeWidth={2} dot={false} />
          </LineChart>
        </ResponsiveContainer>
      </div>
      <div className="game__graph__value">{ payload.value }</div>
    </div>
  )
}

export default Game;